.news-item{
    color: black;
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
    padding-top: 10px;
}

.news-item img{
    border-radius: 10px;
    margin-right: 30px;
    object-fit: cover;
    width: 140px;
    height: 120px;
}

.news-headline {
    font-size: 22px;
    margin-top: 0px;
    margin-bottom: 10px;
    font-weight: bold;
}

.news-content {
    display: flex;
    flex-direction: column !important;
    font: 14px Noto Sans JP;
}



@media only screen
and (min-device-width : 320px)
and (max-device-width : 500px) {  
    .news-item{
        margin-bottom: 5px;
        padding: 10px;
    }

    .news-wrapper {
        margin-bottom: -50px;
    }

    .news-item img{
        border-radius: 10px;
        margin-right: 20px;
        object-fit: cover;
        width: 100px;
        height: 70px;
    }

    .news-headline {
        font-size: 18px;
        margin-top: 10px;
        margin-bottom: 20px;
        font-weight: bold;
    }
    
    .news-content {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        font: 14px Noto Sans JP;
    }

    .news-text {
        margin-left: -50%;
        margin-bottom: 20px;
    }
}