.navigation-panel-list {
    margin-top: 70px;
    font: 24px Bebas Neue;
    letter-spacing: 2px;
    display: flex;
    justify-content: space-between;
    color: #292929;
}
.navigation-panel-list li a{
    color: #a7a7a7;
    margin-left: 40px;
}
.navigation-panel-list li a:hover{
    text-decoration: none;
	color: #fcb118;
}



@media only screen
and (min-device-width : 320px)
and (max-device-width : 560px) {


    .navigation-panel-list {
        margin-top: 10px;
        display: flex;
        align-self: center;
        justify-content: center;
        flex-direction: column;
        font: 30px Bebas Neue;
    }

    .navigation-panel-list li a{
        margin-left: 0px;
    }


}