.header {
    width: 100%;
    padding: 10px;
    background-color: #292929;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    box-shadow: 0 0 5px;
    flex-wrap: wrap;
}
.logo-head {
    margin-top: 10px;
    margin-bottom: 20px;
    margin-left:0;
    image-rendering: crisp-edges;
}




@media only screen
and (min-device-width : 320px)
and (max-device-width : 560px) {

    .header {
        display: flex;
        text-align: center;
        justify-content: center;
        flex-direction: column;
    }
    .logo-head {
        margin-top: 10px;
        image-rendering: crisp-edges;
    }
    

}